import { Stage, useTick } from "@pixi/react";
import { Container, Text } from "@pixi/react-animated";
import { useLocalObservable, useObserver } from "mobx-react";
import * as pixress from "piress-core";
import React from "react";
import { Controller } from "./Controller";
import { generateMockData } from "./mockData";
import { PureTextRenderer } from "./PureTextRenderer";
import { TextView } from "./TextView";

let text = ``;
let text2 = `fugiat sit dolore aliquip exssssssscepteur dolor proident sit. Sunt amet minim do duis ipsum magna reprehenderit elit proid`;
const seqs1 = pixress.create(
  text,
  {
    fontSize: 35,
    fontFamily: "",
    wrapWidth: 790,
    fontStyle: "normal",
    fontWeight: "bold",
    fontVariant: "normal",
    xInterval: 5,
    letterSpacing: 5,
  },
  true,
);
const seqs2 = pixress.create(
  text2,
  {
    fontSize: 35,
    fontFamily: "",
    wrapWidth: 790,
    fontStyle: "normal",
    fontWeight: "bold",
    fontVariant: "normal",
    // xInterval: 5,
    letterSpacing: 5,
  },
  true,
);

console.log(seqs1, seqs2);

const config: pixress.ITypographyOptions = {
  fontSize: 35,
  fontFamily: "",
  wrapWidth: 790,
  fontStyle: "normal",
  fontWeight: "bold",
  fontVariant: "normal",
  // xInterval: 5,
  letterSpacing: 5,
};
const seqs = generateMockData(10000, true).map((txt) =>
  pixress.create(txt, config, true),
);
const controller = new Controller(seqs);
const TextViewDemo = () => {
  React.useEffect(controller.cycle, []);
  // useLocalObservable(() => ({ font }));
  useTick((delta, ticker) => controller.update());
  return useObserver(() => {
    const { skipping, current: datas, playEnd } = controller.state;
    return (
      <Container>
        <Text text={controller.currentState}></Text>
        <Container y={50}>
          {skipping || playEnd ? (
            <PureTextRenderer datas={datas} />
          ) : (
            <TextView
              datas={datas}
              onPlayEnd={(datas) => {
                controller.playEnd(datas.length);
              }}
            />
          )}
        </Container>
      </Container>
    );
  });
};

export default () => {
  return (
    <Stage
      options={{
        backgroundColor: 0xffffff,
      }}
      raf
      renderOnComponentChange
      width={1280}
      height={720}
    >
      <TextViewDemo />
      {/* <TextDemo seqs={seqs[10]}></TextDemo> */}
    </Stage>
  );
};

pixress.printCache();
